import { StringHelper } from "../helper";

export default function MedicationList({ groupData }) {
  return (
    <div>
      <h3>Prescription</h3>
      <table style={{width: "100%"}}>
        <colgroup>
          <col span="1" style={{ width: "30%" }} />
          <col span="1" style={{ width: "70%" }} />
        </colgroup>

        <thead>
          <tr>
            <th>Class</th>
            <th>Medications</th>
          </tr>
        </thead>

        <tbody>
          {groupData.map((drugClass) => {
            return (
              <tr>
                <td>{StringHelper.capitalize(drugClass.name)}</td>
                <td>
                  {drugClass.data.map(StringHelper.capitalize).join(", ")}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
