import styled from "styled-components";
import { codes } from "data/codes";
import { colors } from "./CodeTable";

export const categories = codes.map((category) => category.category);

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) => (props.selected ? "lightgreen" : colors[props.category])};
  padding: 0.5em 1em;
  cursor: pointer;
  font-weight: ${(props) => (props.selected ? "bold" : "none")};
  text-decoration: ${(props) => (props.selected ? "underline" : "none")};

  &:hover {
    background-color: ${(props) => (props.selected ? "lightcoral" : "lightgreen")};
    text-decoration: ${(props) => (props.selected ? "line-through" : "underline")};
  }
`;

export default function CodeSelect({ category, setCategory, section, setSection }) {
  const categoryOnClick = (selectedCategory) => {
    category === selectedCategory ? setCategory(null) : setCategory(selectedCategory);
    setSection(null);
  };

  const sectionOnClick = (selectedSection) => {
    section === selectedSection ? setSection(null) : setSection(selectedSection);
  };

  return (
    <>
      <h3>Category</h3>
      <Container>
        {[...categories, "All"].map((thisCategory) => (
          <Card
            onClick={() => categoryOnClick(thisCategory)}
            selected={thisCategory === category && thisCategory !== "All"}
            category={thisCategory}
          >
            {thisCategory}
          </Card>
        ))}
      </Container>

      {category !== null && category !== "All" && (
        <>
          <h3>Section</h3>
          <Container>
            {codes
              .find((thisCategory) => thisCategory.category === category)
              .sections.map((thisSection) => thisSection.section)
              .map((thisSection) => (
                <Card
                  onClick={() => sectionOnClick(thisSection)}
                  selected={thisSection === section}
                >
                  {thisSection}
                </Card>
              ))}
          </Container>
        </>
      )}
    </>
  );
}
