import PageTemplate from "../PageTemplate";
import { emergencies as emergencyData } from "../../data/medical-emergencies";
import { useState } from "react";
import styled from "styled-components";
import PrescriptionColumn from "../prescriptions/PrescriptionColumn";
import EmergencyInfo from "./EmergencyInfo";
import { useParams } from "react-router-dom";

const Container = styled.div`
  display: flex;
  flex-direction: row;
  column-gap: 1em;
  margin-bottom: 1em;
`;

export default function MedicalEmergency() {
  const { defaultCondition = emergencyData[0].conditions[0].name } = useParams();
  let defaultConditionObject = emergencyData[0].conditions[0];
  const defaultCategoryObject =
    emergencyData.find((category) =>
      category.conditions.find((condition) => {
        if (condition.name.toLowerCase() === defaultCondition.toLowerCase()) {
          defaultConditionObject = condition;
          return category;
        } else return false;
      })
    ) || emergencyData[0];

  const [category, setCategory] = useState(defaultCategoryObject);
  const [condition, setCondition] = useState(defaultConditionObject);

  const categoryOptions = emergencyData.map((otherCategory) => ({
    name: otherCategory.name,
    onClick: () => {
      if (otherCategory.name === category.name) {
        return;
      }

      setCategory(otherCategory);
      setCondition(otherCategory.conditions[0]);
    },
  }));

  const conditionOptions = category.conditions.map((otherCondition) => ({
    name: otherCondition.name,
    onClick: () => {
      if (otherCondition.name === condition.name) {
        return;
      }
      setCondition(otherCondition);
    },
  }));

  return (
    <>
      <Container>
        <PrescriptionColumn title="Category" options={categoryOptions} selected={category.name} />
        <PrescriptionColumn
          title="Condition"
          options={conditionOptions}
          selected={condition.name}
        />
      </Container>
      <EmergencyInfo {...condition} />
    </>
  );
}
