import PageTemplate from "../PageTemplate";
import { prescriptions as prescriptionData } from "../../data/medications";
import { useState } from "react";
import styled from "styled-components";
import PrescriptionColumn from "./PrescriptionColumn";
import PrescriptionInfo from "./PrescriptionInfo";

// eslint-disable-next-line no-unused-vars
import styles from "../../css/prescription-styles.css";
import { useParams } from "react-router-dom";

const Container = styled.div`
  display: flex;
  flex-direction: row;
  column-gap: 1em;
  margin-bottom: 1em;
`;

export default function Prescription() {
  const { defaultCondition = prescriptionData[0].categories[0].name } = useParams();
  let defaultConditionObject = prescriptionData[0].categories[0];
  const defaultCategoryObject =
    prescriptionData.find((category) =>
      category.categories.find((condition) => {
        if (condition.name.toLowerCase() === defaultCondition.toLowerCase()) {
          defaultConditionObject = condition;
          return category;
        } else return false;
      })
    ) || prescriptionData[0];

  const [category, setCategory] = useState(defaultCategoryObject);
  const [condition, setCondition] = useState(defaultConditionObject);
  const [prescription, setPrescription] = useState(condition.prescriptions[0]);

  const categoryOptions = prescriptionData.map((otherCategory) => ({
    name: otherCategory.name,
    onClick: () => {
      if (otherCategory.name === category.name) {
        return;
      }

      setCategory(otherCategory);
      setCondition(otherCategory.categories[0]);
      setPrescription(otherCategory.categories[0].prescriptions[0]);
    },
  }));

  const conditionOptions = category.categories.map((otherCondition) => ({
    name: otherCondition.name,
    onClick: () => {
      if (otherCondition.name === condition.name) {
        return;
      }
      setCondition(otherCondition);
      setPrescription(otherCondition.prescriptions[0]);
    },
  }));

  const prescriptionOptions = condition.prescriptions.map((otherPrescription) => ({
    name: otherPrescription.indication,
    onClick: () => {
      if (otherPrescription.indication !== prescription.indication) {
        setPrescription(otherPrescription);
      }
    },
  }));

  return (
    <>
      <Container>
        <PrescriptionColumn
          title="Category"
          options={categoryOptions}
          selected={category.name}
          style={{ width: "20%" }}
        />
        <PrescriptionColumn
          title="Condition"
          options={conditionOptions}
          selected={condition.name}
          style={{ width: "40%" }}
        />
        <PrescriptionColumn
          title="Indication"
          options={prescriptionOptions}
          selected={prescription.indication}
          style={{ width: "40%" }}
        />
      </Container>
      <PrescriptionInfo {...prescription} />
    </>
  );
}
