import { HashRouter, Routes, Route } from "react-router-dom";

// eslint-disable-next-line no-unused-vars
import "./css/styles.css";

import TitlePage from "./pages/TitlePage";
import { apps } from "./pages/apps";
import PageLayout from "pages/PageLayout";
import styled from "styled-components";
import PageTemplate from "pages/PageTemplate";

function App() {
  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<PageLayout />}>
          <Route
            index
            element={
              <PageTemplate title="Dental Apps" displayTitle={false}>
                <TitlePage />
              </PageTemplate>
            }
          />
          {apps.map((app) => {
            const link = app.link + (app.hasOwnProperty("param") ? `/:${app.param}?` : "");
            return (
              <Route
                path={link}
                element={
                  <PageTemplate
                    title={app.hasOwnProperty("title") ? app.title : app.name}
                    displayTitle={!app.hasOwnProperty("displayTitle") || app.displayTitle}
                    maxWidth={app.hasOwnProperty("maxWidth") && app.maxWidth}
                  >
                    {app.element}
                  </PageTemplate>
                }
              />
            );
          })}
        </Route>
      </Routes>
    </HashRouter>
  );
}

export default App;
