import { useRef, useState } from "react";
import PageTemplate from "../PageTemplate";
import { ClosedExtractionInfo, RCTInfo, RestorationInfo } from "./TreatmentInfo";

import styled from "styled-components";
import { useReactToPrint } from "react-to-print";
import { PrintButton } from "components/Button";
import { selectBarColors } from "components/colors";
import { useParams } from "react-router-dom";

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) => (props.selected ? "lightgreen" : props.color)};
  padding: 0.5em 1em;
  cursor: pointer;
  font-weight: ${(props) => (props.selected ? "bold" : "none")};

  &:hover {
    background-color: lightgreen;
    text-decoration: underline;
  }
`;

export const treatments = [
  {
    name: "Restoration",
    printName: "Restorative Treatment",
    component: RestorationInfo,
  },
  {
    name: "Root Canal Therapy",
    component: RCTInfo,
  },
  {
    name: "Closed Extraction",
    component: ClosedExtractionInfo,
  },
];

treatments.map((treatment, index) => (treatment.color = selectBarColors[index]));

function TreatmentSelect({ treatment, setTreatment }) {
  const treatmentOnClick = (selectedTreatment) => {
    if (treatment.name !== selectedTreatment.name) {
      setTreatment(selectedTreatment);
    }
  };

  return (
    <>
      <h3>Treatment</h3>
      <Container>
        {treatments.map((thisTreatment) => (
          <Card
            onClick={() => treatmentOnClick(thisTreatment)}
            selected={thisTreatment.name === treatment.name}
            color={thisTreatment.color}
          >
            {thisTreatment.name}
          </Card>
        ))}
      </Container>
    </>
  );
}

export default function TreatmentOptionsPage() {
  const { defaultTreatment = treatments[0].name } = useParams();
  const defaultTreatmentObject =
    treatments.find(
      (treatment) => treatment.name.toLowerCase() === defaultTreatment.toLowerCase()
    ) || treatments[0];

  const [treatment, setTreatment] = useState(defaultTreatmentObject);
  const printRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => printRef.current,
    documentTitle: treatment.hasOwnProperty("printName") ? treatment.printName : treatment.name,
  });

  return (
    <>
      <TreatmentSelect {...{ treatment, setTreatment }} />
      <PrintButton handleClick={handlePrint} />
      {treatment.component({ ref: printRef })}
    </>
  );
}
