import { CancelButton, ConfirmButton } from "components/Button";
import { useState } from "react";
import styled from "styled-components";

const ButtonWrapper = styled.div`
  float: right;
  margin: 10px 0 0 20px;
`;

const Button = styled.button`
  padding: 5px;
  cursor: pointer;
`;

const Paragraph = styled.p`
  max-width: 80%;
  text-align: right;
  text-wrap: balance;
`;

export default function ResetButton({ reset }) {
  const [alert, setAlert] = useState(false);

  return (
    <>
      <ButtonWrapper>
        <Button onClick={() => setAlert(true)}>Reset All Text</Button>
      </ButtonWrapper>
      <div className="flex-center justify-end">
        <Paragraph className={alert ? undefined : "hidden"}>
          <span className="red-text">The notes made in this session will be lost</span>, are you
          sure that you want to reset the notes? This will not delete any templates or modules you
          have created.
        </Paragraph>
        <ConfirmButton
          className={alert ? "module-exists-button" : "hidden"}
          handleClick={() => {
            setAlert(false);
            reset();
          }}
        />
        <CancelButton
          className={alert ? "module-exists-button" : "hidden"}
          handleClick={() => setAlert(false)}
        />
      </div>
    </>
  );
}
