import PageTemplate from "../PageTemplate";
import { drugClasses as medicationData } from "../../data/medications";
import { useState, useRef, useCallback, useEffect } from "react";
import styled from "styled-components";
import MedicationMenu from "./MedicationMenu";
import MedicationList from "./MedicationList";
import CodeSearch from "../ada-codes/CodeSearch";
import { StringHelper } from "../helper";

// eslint-disable-next-line no-unused-vars
import styles from "../../css/prescription-styles.css";
import { useParams } from "react-router-dom";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  margin-bottom: 1em;
`;

export default function MedicationsPage() {
  const { defaultGroup = medicationData[0].data[0].name } = useParams();
  let defaultGroupObject = medicationData[0].data[0];
  const defaultSystemObject =
    medicationData.find((system) =>
      system.data.find((group) => {
        if (group.name.toLowerCase() === defaultGroup.toLowerCase()) {
          defaultGroupObject = group;
          return system;
        } else return false;
      })
    ) || medicationData[0];

  const [system, setSystem] = useState(defaultSystemObject);
  const [group, setGroup] = useState(defaultGroupObject);
  const [searchTerm, setSearchTerm] = useState("");

  const systemOptions = medicationData.map((otherSystem) => ({
    name: StringHelper.capitalize(otherSystem.name),
    onClick: () => {
      if (otherSystem.name === system.name) {
        return;
      }
      setSystem(otherSystem);

      const filteredGroups = otherSystem.data.filter((otherGroup) =>
        JSON.stringify(otherGroup).toLowerCase().includes(searchTerm.toLowerCase())
      );

      if (searchTerm && filteredGroups.length > 0) {
        setGroup(filteredGroups[0]);
      } else {
        setGroup(otherSystem.data[0]);
      }
    },
    containsSearchTerm: searchTerm
      ? JSON.stringify(otherSystem).toLowerCase().includes(searchTerm.toLowerCase())
      : false,
  }));

  const groupOptions = system.data.map((otherGroup) => ({
    name: StringHelper.capitalize(otherGroup.name),
    onClick: () => {
      if (otherGroup.name !== group.name) {
        setGroup(otherGroup);
      }
    },
    containsSearchTerm: searchTerm
      ? JSON.stringify(otherGroup).toLowerCase().includes(searchTerm.toLowerCase())
      : false,
  }));

  const searchRef = useRef(null);

  const selectSearch = useCallback((e) => {
    if (e.key === "F3" || (e.ctrlKey && e.key === "f")) {
      e.preventDefault();
      searchRef.current.focus();
    }
  }, []);

  useEffect(() => {
    // attach the event listener
    document.addEventListener("keydown", selectSearch);

    // remove the event listener
    return () => {
      document.removeEventListener("keydown", selectSearch);
    };
  }, [selectSearch]);

  return (
    <>
      <CodeSearch {...{ searchTerm, setSearchTerm, searchRef }} />
      <Container>
        <MedicationMenu
          title="System"
          options={systemOptions}
          selected={StringHelper.capitalize(system.name)}
        />
        <MedicationMenu
          title="Group"
          options={groupOptions}
          selected={StringHelper.capitalize(group.name)}
        />
      </Container>
      <MedicationList groupData={group.data} />
    </>
  );
}
