import { ArrayHelper, CacheHelper, darken } from "./helper";
import { useState } from "react";
import styled from "styled-components";

const ItemButton = styled.button`
  font-size: 0.8em;
  padding: 0.5em 0.8em;
  margin: 0.5em 0.2em;
  background-color: white;
  border-width: 0;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    transform: translateY(-1px);
    background-color: rgb(240, 240, 240);
  }
  &:active {
    transform: translateY(1px);
    background-color: rgb(240, 240, 240);
  }
`;

const todoPath = "todos";

if (localStorage.getItem(todoPath) === null) {
  CacheHelper.localSaveCache(todoPath, []);
}

const colorOptions = [
  {
    name: "green",
    color: "rgb(144,238,144)",
  },
  {
    name: "red",
    color: "rgb(238,144,144)",
  },
  {
    name: "yellow",
    color: "rgb(254, 231, 21)",
  },
  {
    name: "blue",
    color: "rgb(135,206,235)",
  },
  {
    name: "purple",
    color: "rgb(191,144,238)",
  },
];

const Select = styled.select`
  background: none;
  background-color: ${({ $color }) => $color};
  height: 100%;
  aspect-ratio: 1 / 1;
  cursor: pointer;
  appearance: none;
  border-radius: 5px;
  outline: none;

  &:hover {
    background-color: ${({ $color }) => darken($color, "5%")};
  }
`;

const Option = styled.option`
  background-color: ${({ $color }) => $color};
`;

function ColorSelect({ color, setColor }) {
  return (
    <Select
      name="colors"
      id="colors"
      onChange={(e) =>
        setColor(colorOptions.find((option) => option.name === e.target.value).color)
      }
      $color={color}
    >
      {colorOptions.map((option) => (
        <Option value={option.name} $color={option.color} />
      ))}
    </Select>
  );
}

const AddButton = styled.button`
  --background-color: rgb(102, 102, 102);
  padding: var(--input-padding);
  background-color: var(--background-color);
  font-weight: bold;
  color: white;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: var(--input-font-size);

  &:hover {
    background-color: ${darken("var(--background-color)", "15%")};
  }
  &:active {
    transform: translateY(1px);
    background-color: ${darken("var(--background-color)", "30%")};
  }
`;

const AddInput = styled.input`
  padding: var(--input-padding);
  border-radius: 3px 0 0 3px;
  font-size: var(--input-font-size);
  min-width: 400px;
  border-right: none;
`;

const InputContainer = styled.div`
  --input-font-size: 1rem;
  --input-padding: 0.8rem;
  display: flex;
  height: calc(var(--input-font-size) + 2px + 2 * var(--input-padding));
`;

function InputBar({ newTodo, setNewTodo, todos, setTodos }) {
  const [color, setColor] = useState(colorOptions[0].color);

  const addTodo = () => {
    const newTodoObject = {
      task: newTodo,
      color: color,
    };

    const newTodos = [...todos, newTodoObject];
    CacheHelper.localSaveCache(todoPath, newTodos);
    setTodos(newTodos);
    setNewTodo("");
  };

  return (
    <InputContainer>
      <ColorSelect {...{ color, setColor }} />
      <AddInput
        type="text"
        value={newTodo}
        placeholder=" Add item ..."
        onChange={(event) => setNewTodo(event.target.value)}
        onKeyDown={(event) => {
          if (event.key === "Enter") {
            addTodo();
          }
        }}
      />
      <AddButton onClick={addTodo}>ADD</AddButton>
    </InputContainer>
  );
}

const ListItem = styled.li`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${({ $color }) => $color};
  padding: 0.2em 0.5em;
  border-color: black;
  border: 1px solid;
  border-radius: 2px;
  font-size: 1.2em;
  min-width: 400px;
`;

const ListItemInput = styled.input`
  font-size: 1em;
  padding: 0.5em 0;
  border-radius: 3px;
  border: 0;

  &:focus {
    /* outline: none; */
  }
`;

function Todo({ task, color, editTodo, removeTodo, globalEditing, globalSetEditing }) {
  const [editedTask, setEditedTask] = useState(task);
  const [width, setWidth] = useState(task.length);
  const [editing, setEditing] = useState(false);

  const save = () => {
    editTodo(editedTask);
    setEditing(false);
    globalSetEditing(false);
  };

  return (
    <ListItem $color={color}>
      <span style={{ flexGrow: 1, paddingRight: "4px" }}>
        {!editing && task}
        {editing && (
          <ListItemInput
            type="text"
            value={editedTask}
            style={{ width: width + 5 + "ch" }}
            onChange={(event) => {
              console.log(event);
              setEditedTask(event.target.value);
              setWidth(event.target.value.length);
            }}
            autoFocus
            onKeyDown={(event) => {
              if (event.key === "Enter") {
                save();
              }
            }}
          />
        )}
      </span>

      {!editing && (
        <ItemButton
          onClick={() => {
            if (globalEditing) {
              return;
            }
            setEditedTask(task);
            setEditing(true);
            globalSetEditing(true);
          }}
        >
          Edit
        </ItemButton>
      )}
      {editing && (
        <ItemButton onClick={save} style={{ backgroundColor: "#f44336", color: "white" }}>
          Save
        </ItemButton>
      )}
      <ItemButton
        onClick={() => {
          save();
          removeTodo();
        }}
      >
        Delete
      </ItemButton>
    </ListItem>
  );
}

const ListGroup = styled.ul`
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  margin-top: 20px;
  width: max-content;
`;

function TodoList({ todos, setTodos }) {
  const [editing, setEditing] = useState(false);
  return (
    <ListGroup>
      {todos.map((todo, index) => {
        const editTodo = (editedTask) => {
          const newTodo = { ...todo, task: editedTask };
          const newTodos = ArrayHelper.replace(todos, index, newTodo);
          CacheHelper.localSaveCache(todoPath, newTodos);
          setTodos(newTodos);
        };

        const removeTodo = () => {
          const newTodos = ArrayHelper.remove(todos, todo);
          CacheHelper.localSaveCache(todoPath, newTodos);
          setTodos(newTodos);
        };

        return (
          <Todo
            {...{ editTodo, removeTodo }}
            globalEditing={editing}
            globalSetEditing={setEditing}
            {...todo}
          />
        );
      })}
    </ListGroup>
  );
}

function TodoComponent({ todoData }) {
  const [todos, setTodos] = useState(todoData);
  const [newTodo, setNewTodo] = useState("");

  return (
    <>
      <InputBar {...{ newTodo, setNewTodo, todos, setTodos }} />

      <TodoList {...{ todos, setTodos }} />

      {todos.length === 0 && "No Todos Found"}
    </>
  );
}

export default function TodoPage() {
  const todoData = CacheHelper.localReadCache(todoPath);
  return <TodoComponent todoData={todoData} />;
}
