import { StringHelper } from "pages/helper";
import styled from "styled-components";

const Link = styled.a`
  text-decoration: none;
  color: #3366cc;
  background: none;

  &:hover {
    text-decoration: underline;
  }
`;
const TrHeader = styled.td`
  background-color: #facfce;
  font-weight: bold;
  text-align: center;
`;

function TableTemplate(props) {
  const { data, printRef, children } = props;

  return (
    <div ref={printRef} {...props}>
      <h2>{data.name}</h2>
      <table>{children}</table>
      <p>
        <strong>Source</strong>: {data.source}{" "}
        {data.hasOwnProperty("link") && <Link href={data.link}>Link</Link>}
      </p>
    </div>
  );
}

function ClassificationImageHorizontalTable(props) {
  const { data, imageSize = "120px" } = props;

  return (
    <TableTemplate {...props}>
      <thead>
        <tr>
          <th colSpan="100%">{data.name}</th>
        </tr>
      </thead>

      <tbody>
        <tr>
          {data.classes.map((classification) => (
            <TrHeader>{classification.class}</TrHeader>
          ))}
        </tr>

        <tr>
          {data.classes.map((classification) => (
            <td>
              <div
                style={{
                  height: imageSize,
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <img
                  src={classification.image}
                  alt={classification.class}
                  style={{ height: "100%" }}
                />
              </div>
              <div style={{ textAlign: "center" }}>
                {classification.description}
              </div>
            </td>
          ))}
        </tr>
      </tbody>
    </TableTemplate>
  );
}

function CategoryClassificationImagesTable(props) {
  const {
    data,
    imageSize = "120px",
    cellWidth = "200px",
    cellGap = "20px",
  } = props;

  return (
    <TableTemplate {...props}>
      <thead>
        <tr>
          <th colSpan="100%">{data.name}</th>
        </tr>
      </thead>

      <tbody>
        {data.categories.map((category) => (
          <tr>
            <TrHeader>{category.name}</TrHeader>
            <td style={{ display: "flex", columnGap: cellGap }}>
              {category.classes.map((classification) => (
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    width: cellWidth,
                  }}
                >
                  <div>{classification.class}</div>
                  <div style={{ height: imageSize }}>
                    <img
                      src={classification.image}
                      alt={classification.class}
                      style={{ height: "100%" }}
                    />
                  </div>
                  <div style={{ textAlign: "center" }}>
                    {classification.description}
                  </div>
                </div>
              ))}
            </td>
          </tr>
        ))}
      </tbody>
    </TableTemplate>
  );
}

function CategoryClassificationImagesHorizontalTable(props) {
  const { data, imageSize = "120px", cellWidth = "200px" } = props;

  return (
    <TableTemplate {...props}>
      <thead>
        <tr>
          {data.categories.map((category) => (
            <TrHeader>{category.name}</TrHeader>
          ))}
        </tr>
      </thead>

      <tbody>
        <tr>
          {data.categories.map((category) => (
            <td style={{ verticalAlign: "top" }}>
              {category.classes.map((classification, index) => (
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    width: cellWidth,
                    marginTop: index === 0 ? 0 : "10px",
                  }}
                >
                  <div>
                    <strong>{classification.class}</strong>
                  </div>
                  <div style={{ height: imageSize }}>
                    <img
                      src={classification.image}
                      alt={classification.class}
                      style={{ height: "100%" }}
                    />
                  </div>
                  <div style={{ textAlign: "center" }}>
                    {classification.description}
                  </div>
                </div>
              ))}
            </td>
          ))}
        </tr>
      </tbody>
    </TableTemplate>
  );
}

function InformationTable(props) {
  const { data } = props;

  return (
    <TableTemplate {...props}>
      <thead>
        <tr>
          <th>Classification</th>
          {data.attributes.map((attribute) => (
            <th>{StringHelper.capitalize(attribute)}</th>
          ))}
        </tr>
      </thead>

      <tbody>
        {data.classes.map((classification) => (
          <tr>
            <TrHeader>{classification.class}</TrHeader>
            {data.attributes.map((attribute) => (
              <td>{classification[attribute]}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </TableTemplate>
  );
}

