export function ThirdMolarClassificationsTable(props) {
  const { printRef } = props;
  delete props.printRef;

  return (
    <div ref={printRef}>
      <h2>Summary of third molar classifications</h2>
      <div style={{ width: "min(100%, 700px)" }}>
        <img
          src={require("images/dental/oral-surgery/third-molar-classification-summary.png")}
          alt="Third molar classification summary"
          style={{ width: "100%" }}
        />
        <p>
          <strong>Source</strong>: Johnson N. Third Molar Surgery [unpublished
          lecture notes]. DENT3060: Dental Practice IIIA, Herston (AU):
          University of Queensland; lecture given 2021.
        </p>
      </div>
      <ClassificationImageHorizontalTable
        data={thirdMolarRamus}
        imageSize="150px"
        {...props}
      />
      <ClassificationImageHorizontalTable
        data={thirdMolarImpaction}
        imageSize="150px"
        {...props}
      />
      <ClassificationImageHorizontalTable
        data={thirdMolarAngulation}
        {...props}
      />
    </div>
  );
}
