import { PrintButton } from "components/Button";
import PageTemplate from "pages/PageTemplate";
import { useRef, useState } from "react";
import { useReactToPrint } from "react-to-print";
import styled from "styled-components";
import { ToothDisease } from "./DiseaseInfo";
import { selectBarColors } from "components/colors";
import { useParams } from "react-router-dom";

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) => (props.selected ? "lightgreen" : props.color)};
  padding: 0.5em 1em;
  cursor: pointer;
  font-weight: ${(props) => (props.selected ? "bold" : "none")};

  &:hover {
    background-color: lightgreen;
    text-decoration: underline;
  }
`;

const diseases = [
  {
    name: "Tooth Disease",
    component: ToothDisease,
    color: "#F7CAC9",
  },
  {
    name: "Gum Disease",
    component: ToothDisease,
    color: "plum",
  },
];

diseases.map((disease, index) => (disease.color = selectBarColors[index]));

function DiseaseSelect({ disease, setDisease }) {
  const treatmentOnClick = (selectedTreatment) => {
    if (disease.name !== selectedTreatment.name) {
      setDisease(selectedTreatment);
    }
  };

  return (
    <>
      <h3>Treatment</h3>
      <Container>
        {diseases.map((thisTreatment) => (
          <Card
            onClick={() => treatmentOnClick(thisTreatment)}
            selected={thisTreatment.name === disease.name}
            color={thisTreatment.color}
          >
            {thisTreatment.name}
          </Card>
        ))}
      </Container>
    </>
  );
}

export default function DiseasesPage() {
  const { defaultDisease = diseases[0].name } = useParams();
  const defaultDiseaseObject =
    diseases.find((treatment) => treatment.name.toLowerCase() === defaultDisease.toLowerCase()) ||
    diseases[0];

  const [disease, setDisease] = useState(defaultDiseaseObject);
  const printRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => printRef.current,
    documentTitle: disease.hasOwnProperty("printName") ? disease.printName : disease.name,
  });

  return (
    <>
      <DiseaseSelect {...{ disease, setDisease }} />
      <PrintButton handleClick={handlePrint} />
      {disease.component({ printRef })}
    </>
  );
}
