import { useState, useCallback, useRef, useEffect } from "react";
import PageTemplate from "../PageTemplate";
import CodeTable from "./CodeTable";
import CodeSelect, { categories } from "./CodeSelect";
import CodeSearch from "./CodeSearch";
import { useParams } from "react-router-dom";

export default function AdaCodes() {
  const { defaultCategory = null } = useParams();
  const defaultCategoryState = defaultCategory === null ? null : categories.find(category => category.toLowerCase() === defaultCategory.toLowerCase()) || null

  const [category, setCategory] = useState(defaultCategoryState);
  const [section, setSection] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");

  const searchRef = useRef(null);

  const selectSearch = useCallback((e) => {
    if (e.key === "F3" || (e.ctrlKey && e.key === "f")) {
      e.preventDefault();
      searchRef.current.focus();
    }
  }, []);

  useEffect(() => {
    // attach the event listener
    document.addEventListener("keydown", selectSearch);

    // remove the event listener
    return () => {
      document.removeEventListener("keydown", selectSearch);
    };
  }, [selectSearch]);

  return (
    <>
      <CodeSearch {...{ searchTerm, setSearchTerm, searchRef }} />
      <CodeSelect {...{ category, setCategory, section, setSection }} />
      <CodeTable {...{ category, section, searchTerm }} />
    </>
  );
}
