import { StringHelper } from "../helper";

export default function PrescriptionInfo({ medications, notes = [], source }) {
  const medicationString = (name) =>
    Array.isArray(name)
      ? name.map(StringHelper.capitalize).join(" + ")
      : StringHelper.capitalize(name);

  return (
    <div>
      <h3>Prescription</h3>
      <table style={{ width: "100%" }}>
        <colgroup>
          <col span="1" style={{ width: "5%" }} />
          <col span="1" style={{ width: "20%" }} />
          <col span="1" style={{ width: "75%" }} />
        </colgroup>

        <thead>
          <tr>
            <th>Index</th>
            <th>Medication</th>
            <th>Dose and frequency</th>
          </tr>
        </thead>

        <tbody>
          {medications.map((medication, index) => {
            if (Array.isArray(medication)) {
              return medication.map((option, optionIndex) => (
                <tr>
                  {optionIndex === 0 && (
                    <td rowspan={medication.length} className="index">
                      {index + 1}
                    </td>
                  )}
                  <td>{medicationString(option.name)}</td>
                  <td>
                    {option.dose.split("\n").map((text) => (
                      <p>{text}</p>
                    ))}
                  </td>
                </tr>
              ));
            }

            return (
              <tr>
                <td className="index">{index + 1}</td>
                <td>{medicationString(medication.name)}</td>
                <td>
                  {medication.dose.split("\n").map((text) => (
                    <div>{text}</div>
                  ))}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      {notes.length > 0 &&
        (notes.length === 1 ? (
          <p>Note: {notes[0]}</p>
        ) : (
          <>
            <p>Notes:</p>
            {notes.map((note) => (
              <p>- {note}</p>
            ))}
          </>
        ))}

      <p>Source: {source}.</p>
    </div>
  );
}
