const types = {
  treatment: "treatment",
  category: "category",
  subcategory: "subcategory",
  condition: "condition",
  factor: "factor",
};

for (const [treatment, text] of Object.entries(treatments)) {
  treatments[treatment] = {
    text: text,
    type: types.treatment,
  };
}

const resorptionTreatment = {
  type: types.subcategory,
  text: "Root resorption",
  children: [
    {
      type: types.condition,
      text: "External",
      treatments: [treatments.rootCanalTreatment],
    },
    {
      type: types.condition,
      text: "Internal",
      treatments: [treatments.rootCanalTreatment],
    },
    {
      type: types.condition,
      text: "Invasive cervical",
      treatments: [],
    },
  ],
};

