import { Link, NavLink } from "react-router-dom";
import styled from "styled-components";

import { apps } from "../pages/apps";

const NavigationBar = styled.nav`
  display: flex;
  flex-wrap: wrap;
  row-gap: 1em;
`;

function Header() {
  return (
    <header className="page-header">
      <h1>
        <Link to="/">Dental Apps</Link>
      </h1>
      <NavigationBar className="main-menu">
        {apps.map((app) => (
          <NavLink to={app.link}>{app.name}</NavLink>
        ))}
      </NavigationBar>
    </header>
  );
}

export default Header;
