import styled from "styled-components";
import { classificationCategories } from "./classificationsCategories.js";

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) => (props.selected ? "lightgreen" : props.color)};
  padding: 0.5em 1em;
  cursor: pointer;
  font-weight: ${(props) => (props.selected ? "bold" : "none")};

  &:hover {
    background-color: lightgreen;
    text-decoration: underline;
  }
`;

export default function ClassificationSelect({
  category,
  setCategory,
  classification,
  setClassification,
  renderComponent,
}) {
  const categoryOnClick = (selectedCategory) => {
    if (category !== selectedCategory) {
      setCategory(selectedCategory.category);
      classOnClick(selectedCategory.classes[0]);
    }
  };

  const classOnClick = (selectedClassification) => {
    if (classification.name !== selectedClassification.name) {
      setClassification(selectedClassification);
      renderComponent(selectedClassification);
    }
  };

  return (
    <>
      <h3>Category</h3>
      <Container>
        {classificationCategories.map((thisCategory) => (
          <Card
            onClick={() => categoryOnClick(thisCategory)}
            selected={thisCategory.category === category}
            color={thisCategory.color}
          >
            {thisCategory.category}
          </Card>
        ))}
      </Container>

      <h3>Classification</h3>
      <Container>
        {classificationCategories
          .find((thisCategory) => thisCategory.category === category)
          .classes.map((thisClassification) => (
            <Card
              onClick={() => classOnClick(thisClassification)}
              selected={thisClassification.name === classification.name}
            >
              {thisClassification.name}
            </Card>
          ))}
      </Container>
    </>
  );
}
