import { emergencyConditions as emergencyData } from "data/treatment-decisions";
import FlowDiagram from "components/TreeDiagram";
import { useState } from "react";

const displayLimit = 20;

function typeCount(tree, count = {}) {
  const { type, children = [], treatments = [], minimise=false } = tree;

  const branches = [...children, ...treatments];

  if (count.hasOwnProperty(type)) {
    count[type]++;
  } else {
    count[type] = 1;
  }
  if (branches.length > 0 && (!minimise || type === "root")) {
    for (const branch of branches) {
      typeCount(branch, count);
    }
  }
  return count;
}

function dataToNode(data, typeCounts, treeData, setTreeData = null) {
  const { text, children = [], treatments = [], minimise=false, onClick=null, selectable=true } = data;

  let tree = { name: text };

  const branches = [...children, ...treatments];

  if (onClick !== null && selectable) {
    console.log('here')
    tree.onClick = onClick;
  }

  if (branches.length > 0) {
    if (typeCounts[branches[0].type] < displayLimit) {
      tree.children = branches.map((child) => dataToNode(child, typeCounts, treeData, setTreeData));
    } else {
      tree.truncated = true;
    }

    if (data.type !== "root" && setTreeData !== null && selectable) {
      tree.onClick = () =>
        setTreeData({
          text: "⋮",
          type: "root",
          children: [{ ...data, minimise: false }],
          onClick: () => setTreeData(treeData)
        });
    }
  }

  if (minimise) {
    tree.truncated = true;
  }

  return tree;
}

function dataToTree(data, setTreeData) {
  const typeCounts = typeCount(data);

  return dataToNode(data, typeCounts, data, setTreeData);
}

const defaultTree = {
  text: "Emergency Conditions",
  type: "root",
  children: emergencyData,
};

export default function EmergencyOptionsTree() {
  const [treeData, setTreeData] = useState(defaultTree);

  return (
    <>
      <button
        id="reset-button"
        onClick={() => setTreeData(defaultTree)}
        style={{ display: defaultTree !== treeData ? "block" : "none" }}
      >
        Back to root
      </button>
      <FlowDiagram data={dataToTree(treeData, setTreeData)} />
    </>
  );
}
