import PropTypes from "prop-types";
import { useLayoutEffect } from "react";
import styled from "styled-components";

const MainContent = styled.div`
  max-width: ${({ maxWidth = "none" }) => maxWidth};
  margin-left: auto;
  margin-right: auto;
`;

function PageTemplate({ title, children, displayTitle=true, maxWidth="none" }) {
  document.title = title;

  /* useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }); */

  return (
    <MainContent maxWidth={maxWidth}>
      {displayTitle && <h2>{title}</h2>}
      {children}
    </MainContent>
  );
}

// Type checks props to ensure that title is a string and is not empty
PageTemplate.propTypes = {
  title: PropTypes.string.isRequired,
};

export default PageTemplate;
