import styled from "styled-components";

const Button = styled.button`
  cursor: pointer;
`;

export function RemoveButton({ handleClick, className = "" }) {
  return (
    <Button className={"remove-button " + className} onClick={handleClick}>
      -
    </Button>
  );
}

export function AddButton({ handleClick, className = "" }) {
  return (
    <Button className={"add-button " + className} onClick={handleClick}>
      +
    </Button>
  );
}

export function ConfirmButton({ handleClick, className = "" }) {
  return (
    <Button className={"confirm-button " + className} onClick={handleClick}>
      <img src={require("images/icons/tick-icon.png")} alt="confirm-icon" />
    </Button>
  );
}

export function CancelButton({ handleClick, className = "" }) {
  return (
    <Button className={"confirm-button " + className} onClick={handleClick}>
      <img src={require("images/icons/cross-icon.png")} alt="cancel-icon" />
    </Button>
  );
}

export function SaveButton({ handleClick, saved }) {
  return (
    <Button className="save-button" onClick={handleClick}>
      <img
        src={require("images/icons/saved-icon.png")}
        alt="saved-icon"
        className={saved ? undefined : "hidden"}
      />
      <img
        src={require("images/icons/not-saved-icon.png")}
        alt="not-saved-icon"
        className={saved ? "hidden" : undefined}
      />
    </Button>
  );
}

export function EditButton({ handleClick, editing }) {
  return (
    <Button key="edit-button" className="edit-button" onClick={handleClick}>
      <img
        src={require("images/icons/edit-icon.png")}
        alt="edit-icon"
        className={editing ? "hidden" : undefined}
      />
      <img
        src={require("images/icons/save-icon.png")}
        alt="editing-icon"
        className={editing ? undefined : "hidden"}
      />
    </Button>
  );
}

export function Hamburger(props) {
  return (
    <img
      src={require("images/icons/hamburger.png")}
      alt="drag-handle"
      className="hamburger"
      {...props}
    />
  );
}

const PrintContainer = styled.p`
  float: right;
  display: flex;
  align-items: center;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
`;

const PrintImage = styled.img`
  margin-right: 3px;
`;

export function PrintButton({ handleClick }) {
  return (
    <PrintContainer onClick={handleClick}>
      <PrintImage
        src={require("images/icons/print-icon.png")}
        alt="print-icon"
      />
      Print
    </PrintContainer>
  );
}
