import { selectBarColors } from "components/colors";
import { codes } from "data/codes";
import styled from "styled-components";

const items = [];
codes.map((category) =>
  category.sections.map((section) => {
    for (const [item, description] of Object.entries(section.codes)) {
      items.push({
        item: item,
        description: description,
        category: category.category,
        section: section.section,
      });
    }
  })
);

export const colors = { All: selectBarColors.default };
codes.map((category, index) => (colors[category.category] = selectBarColors[index]));

const ItemCell = styled.td`
  background-color: ${(props) => colors[props.category]};
  width: 3em;
`;

export default function CodeTable({ category = null, section = null, searchTerm }) {
  const searchTermLower = searchTerm.toLowerCase();
  return (
    <>
      <h3>Item Codes</h3>
      <table style={{ width: "100%" }}>
        <thead>
          <tr>
            <th>Item</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          {items.map((item) => {
            if (
              (category === null || category === item.category || category === "All") &&
              (section === null || section === item.section) &&
              Object.values(item).some((value) => value.toLowerCase().includes(searchTermLower))
            )
              return (
                <tr>
                  <ItemCell category={item.category}>{item.item}</ItemCell>
                  <td>{item.description}</td>
                </tr>
              );
          })}
        </tbody>
      </table>
    </>
  );
}
