import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Title = styled.h3`
  align-self: center;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) => (props.selected ? "lightgreen" : "#666")};
  padding: 0.5em 1em;
  color: ${(props) => (props.selected ? "black" : "white")};
  cursor: pointer;

  &:hover {
    background-color: ${(props) => (props.selected ? "lightgreen" : "white")};
    color: black;
  }
`;

/*
  options: list(dict(name, onClick))
*/
export default function PrescriptionColumn({ title, options, selected, style }) {
  return (
    <Container style={style}>
      <Title>{title}</Title>
      {options.map((option) => (
        <Card onClick={option.onClick} selected={option.name === selected}>
          {option.name}
        </Card>
      ))}
    </Container>
  );
}
