// Colors
export function darken(rgb, percent) {
  return `color-mix(in srgb, ${rgb}, black ${percent})`;
}

export function lighten(rgb, percent) {
  return `color-mix(in srgb, ${rgb}, white ${percent})`;
}

export function transparent(rgb, percent) {
  return `color-mix(in srgb, ${rgb}, transparent ${percent})`;
}

export class StringHelper {
  static capitalize(string) {
    return string[0].toUpperCase() + string.slice(1);
  }

  static leftValue(string) {
    return string
      .split(" ")
      .map((substr, index) => (index === 0 ? substr : this.capitalize(substr)))
      .join("");
  }

  static isAlpha(ch) {
    return /^[A-Z]$/i.test(ch);
  }
}

export class ArrayHelper {
  static replace(array, index, element) {
    return [...array.slice(0, index), element, ...array.slice(index + 1)];
  }

  static insert(array, index, element) {
    return [...array.slice(0, index), element, ...array.slice(index)];
  }

  static remove(array, element) {
    const index = array.indexOf(element);
    return [...array.slice(0, index), ...array.slice(index + 1)];
  }

  static move(array, fromIndex, toIndex) {
    const newArray = [...array];
    const element = newArray.splice(fromIndex, 1)[0];
    newArray.splice(toIndex, 0, element);
    return newArray;
  }
}

let messageLocalSaveCache = (key) => `Saved a localStorage file called "${key}".`;
let messageLocalReadCache = (key) => `Read a localStorage file called "${key}".`;
let messageSessionSaveCache = (key) => `Saved a sessionStorage file called "${key}".`;
let messageSessionReadCache = (key) => `Read a sessionStorage file called "${key}".`;

export class CacheHelper {
  static localSaveCache(key, data) {
    localStorage.setItem(key, JSON.stringify(data));
    console.log(messageLocalSaveCache(key));
  }

  static localReadCache(filename) {
    const data = localStorage.getItem(filename);
    console.log(messageLocalReadCache(filename));
    return JSON.parse(data);
  }

  static sessionSaveCache(key, data) {
    sessionStorage.setItem(key, JSON.stringify(data));
    console.log(messageLocalSaveCache(key));
  }

  static sessionReadCache(filename) {
    const data = sessionStorage.getItem(filename);
    console.log(messageLocalReadCache(filename));
    return JSON.parse(data);
  }
}
