function FeedbackIndicator({ copiedToClipboard, children }) {
  return (
    <div className={copiedToClipboard ? "copied" : undefined} id="feedback">
      {children}
      <span id="checkmark" />
      <p>
        {copiedToClipboard ? "Copied to Cipboard" : "Not Copied to Clipboard"}
      </p>
    </div>
  );
}

export default function OutputText({
  modules,
  copiedToClipboard,
  setCopiedToClipboard,
}) {
  function getOutputText() {
    return modules
      .filter((module) => module.type === "module")
      .map((module) => module.content)
      .join("\n\n");
  }

  function copyOutputToClipboard() {
    navigator.clipboard.writeText(getOutputText());
    setCopiedToClipboard(true);
  }

  return (
    <>
      <br />
      <h3>Output text</h3>
      <p className="output-text">{getOutputText()}</p>

      <FeedbackIndicator
        copiedToClipboard={copiedToClipboard}
      >
        <button id="copy-to-clipboard" onClick={copyOutputToClipboard}>
          Copy to Clipboard
        </button>
      </FeedbackIndicator>
    </>
  );
}
