import { useEffect, useState } from "react";
import * as defaultData from "data/treatment-note-modules";
import { ArrayHelper, CacheHelper } from "pages/helper";
import ModuleTemplate from "./ModuleTemplate";
import TemplateSelect from "./TemplateSelect";
import ModuleSelect from "./ModuleSelect";
import OutputText from "./OutputText";
import { DragDropContext, Droppable, Draggable } from "@hello-pangea/dnd";
import { Hamburger } from "components/Button";

// eslint-disable-next-line no-unused-vars
import "css/treatment-note-styles.css";
import ResetButton from "./ResetButton";

const moduleDataPath = "module-data";
const disciplineDataPath = "discipline-data";
export const templateDataPath = "template-data";
const sessionModulePath = "session-modules"

if (localStorage.getItem(moduleDataPath) === null) {
  CacheHelper.localSaveCache(moduleDataPath, defaultData.moduleData);
}

if (localStorage.getItem(disciplineDataPath) === null) {
  CacheHelper.localSaveCache(disciplineDataPath, defaultData.disciplineData);
}

if (localStorage.getItem(templateDataPath) === null) {
  CacheHelper.localSaveCache(templateDataPath, defaultData.templateData);
}

let moduleData = CacheHelper.localReadCache(moduleDataPath);
let disciplineData = CacheHelper.localReadCache(disciplineDataPath);
let templateData = CacheHelper.localReadCache(templateDataPath);

export function saveData(modules, disciplines) {
  CacheHelper.localSaveCache(moduleDataPath, modules);
  CacheHelper.localSaveCache(disciplineDataPath, disciplines);
}

export default function TreatmentNotesPage() {
  const [defaultModules, setDefaultModules] = useState(moduleData);
  const [disciplines, setDisciplines] = useState(disciplineData);
  const [templates, setTemplates] = useState(templateData);

  const startingModules = [
    {
      key: 0,
      type: "module",
      ...defaultModules.find((module) => module.name === "Appointment purpose"),
    },
    { key: 1, type: "module-select" },
    {
      key: 2,
      type: "module",
      ...defaultModules.find((module) => module.name === "Next visit"),
    },
  ];
  const [modules, setModules] = useState(sessionStorage.getItem(sessionModulePath) === null ? startingModules : CacheHelper.sessionReadCache(sessionModulePath));
  const [copiedToClipboard, setCopiedToClipboard] = useState(false);

  useEffect(() => () => CacheHelper.sessionSaveCache(sessionModulePath, modules), [modules]);

  const addModule = (moduleName, index = modules.length) => {
    setCopiedToClipboard(false);

    setModules(
      ArrayHelper.insert(modules, index, {
        type: "module",
        key: Math.max(...modules.map((module) => module.key)) + 1,
        ...defaultModules.find((module) => module.name === moduleName),
      })
    );
  };

  const removeModule = (module) => {
    setModules(ArrayHelper.remove(modules, module));
  };

  const saveModule = (updatedModule) => {
    const index = defaultModules.findIndex((module) => module.name === updatedModule.name);

    const newDefaultModules = ArrayHelper.replace(defaultModules, index, {
      name: updatedModule.name,
      content: updatedModule.content,
    });

    setDefaultModules(newDefaultModules);

    saveData(newDefaultModules, disciplines);
  };

  const deleteModule = (moduleName, fromDiscipline) => {
    const oldDiscipline = disciplines.find((discipline) => discipline.name === fromDiscipline);

    const newDiscipline = {
      name: oldDiscipline.name,
      modules: oldDiscipline.modules.filter((module) => module !== moduleName),
    };

    const newDisciplines = ArrayHelper.replace(
      disciplines,
      disciplines.indexOf(oldDiscipline),
      newDiscipline
    );

    // If the module is not used anywhere else
    if (disciplines.filter((discipline) => discipline.modules.includes(moduleName)).length <= 1) {
      setDefaultModules(defaultModules.filter((module) => module.name !== moduleName));
    }

    setDisciplines(newDisciplines);
  };

  // function ModuleSelectCard({ module, index }) {
  //   return (
  //     <Draggable draggableId={module.key.toString()} index={index}>
  //       {(provided) => (
  //         <div ref={provided.innerRef} {...provided.draggableProps}>
  //           <ModuleSelect
  //             dragHandle={<Hamburger {...provided.dragHandleProps} />}
  //             {...{
  //               modules: defaultModules,
  //               setModules: setDefaultModules,
  //               disciplines,
  //               setDisciplines,
  //               selectedDiscipline,
  //               setSelectedDiscipline,
  //               disciplineData,
  //               addModule: (newModule) =>
  //                 addModule(newModule, modules.indexOf(module)),
  //               deleteModule,
  //               editing,
  //               setEditing,
  //             }}
  //           />
  //         </div>
  //       )}
  //     </Draggable>
  //   );
  // }

  // function ModuleTextareaCard({ module, index }) {
  //   // Module textarea
  //   const handleChange = (content) => {
  //     setCopiedToClipboard(false);

  //     const index = modules.indexOf(module);
  //     setModules(ArrayHelper.replace(modules, index, { ...module, content }));
  //   };
  //   return (
  //     <Draggable draggableId={module.key.toString()} index={index}>
  //       {(provided) => (
  //         <div ref={provided.innerRef} {...provided.draggableProps}>
  //           <ModuleTemplate
  //             handleChange={handleChange}
  //             removeModule={() => removeModule(module)}
  //             saveModule={() => saveModule(module)}
  //             dragHandle={<Hamburger {...provided.dragHandleProps} />}
  //             {...module}
  //           />
  //         </div>
  //       )}
  //     </Draggable>
  //   );
  // }

  const onDragEnd = (result) => {
    const { destination, source } = result;

    if (!destination) {
      return;
    }

    if (destination.droppableId === source.draggableId && destination.index === source.index) {
      return;
    }

    console.log(ArrayHelper.move(modules, source.index, destination.index));
    setModules(ArrayHelper.move(modules, source.index, destination.index));
  };

  return (
    <>
      <TemplateSelect {...{ templates, setTemplates, modules, setModules }}></TemplateSelect>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="treatment-cards">
          {(droppableProvided) => (
            <div ref={droppableProvided.innerRef} {...droppableProvided.droppableProps} className="relative">
              <ResetButton reset={() => setModules(startingModules)} />
              {modules.map((module, index) => {
                if (module.type === "module-select") {
                  // return <ModuleSelectCard {...{ module, index }} />;
                  return (
                    <Draggable
                      draggableId={module.key.toString()}
                      key={module.key.toString()}
                      index={index}
                    >
                      {(provided) => (
                        <div ref={provided.innerRef} {...provided.draggableProps}>
                          <ModuleSelect
                            dragHandle={<Hamburger {...provided.dragHandleProps} />}
                            {...{
                              modules: defaultModules,
                              setModules: setDefaultModules,
                              disciplines,
                              setDisciplines,
                              disciplineData,
                              addModule: (newModule) =>
                                addModule(newModule, modules.indexOf(module)),
                              deleteModule,
                            }}
                          />
                        </div>
                      )}
                    </Draggable>
                  );
                } else {
                  // return <ModuleTextareaCard {...{ module, index }} />;
                  const handleChange = (content) => {
                    setCopiedToClipboard(false);

                    const index = modules.indexOf(module);
                    setModules(
                      ArrayHelper.replace(modules, index, {
                        ...module,
                        content,
                      })
                    );
                  };
                  return (
                    <Draggable
                      draggableId={module.key.toString()}
                      key={module.key.toString()}
                      index={index}
                    >
                      {(provided) => (
                        <div ref={provided.innerRef} {...provided.draggableProps}>
                          <ModuleTemplate
                            handleChange={handleChange}
                            removeModule={() => removeModule(module)}
                            saveModule={() => saveModule(module)}
                            dragHandle={<Hamburger {...provided.dragHandleProps} />}
                            {...module}
                          />
                        </div>
                      )}
                    </Draggable>
                  );
                }
              })}

              {droppableProvided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>

      <OutputText {...{ modules, copiedToClipboard, setCopiedToClipboard }} />
    </>
  );
}
