        description:
          "Syncope is an acute hypotensive episode, resulting in transient global cerebral hypoperfusion and loss of consciousness, which usually leads to loss of postural tone and falling. Brief periods of twitching or convulsive movements can sometimes accompany syncope but should not be confused with epilepsy. Secondary injury (following a fall) can result in significant harm. The onset is almost always rapid; recovery is usually rapid, spontaneous and complete.\nPresyncope is a condition in which the patient feels as though syncope is imminent; signs and symptoms include light-headedness, nausea, anxiety, pallor, sweating and tinnitus.",
