        description:
          "Hyperventilation syndrome can occur when a patient hyperventilates (overbreathes). It is common, and is often associated with anxiety or panic attacks.\nSymptoms include light-headedness, dizziness, shortness of breath, feeling of panic and impending death, blurred vision, tingling in the fingers, toes and lips, and feeling of detachment.\nSigns include rapid breathing, occasional deep sighing breaths, rapid heart rate, altered consciousness, involuntary contraction of the hands and fingers.",
