import TextareaAutosize from "react-textarea-autosize";
import { useState } from "react";
import { RemoveButton, SaveButton } from "../../components/Button";

function ModuleTextarea({ handleChange, content }) {
  return (
    <TextareaAutosize
      value={content}
      className="module-textarea border-box"
      onChange={handleChange}
    />
  );
}

export default function ModuleTemplate({
  name,
  content,
  handleChange,
  removeModule,
  saveModule,
  dragHandle,
}) {
  const [saved, setSaved] = useState(true);

  return (
    <div className="white-background">
      <h3 className="flex-center">
        {dragHandle}
        {name}
        <RemoveButton handleClick={removeModule} />
        <SaveButton
          handleClick={() => {
            saveModule();
            setSaved(true);
          }}
          saved={saved}
        />
      </h3>

      <ModuleTextarea
        handleChange={(event) => {
          handleChange(event.target.value);
          setSaved(false);
        }}
        content={content}
      />
    </div>
  );
}
