    name: "Pulp considerations",
    laymanDescription:
      "A tooth's pulp is capable of recoverying from mild insults. There are two cases in which the pulp can no longer recover from a disease state and needs to be removed: /liIf bacteria invade through the hard tooth structure into the pulp and survive using nutrients from the pulp. The pulp's immune system can only slow bacteria's progression but cannot eliminate the bacteria once mature biofilm is formed. The bacteria would slowly infect the rest of the pulp and then spread through the tip of the tooth. The tooth would continue to act as a source of infection until the bacteria are cleaned from the pulp space. /liAlthough pulp can recover from trauma, it never recovers fully to health. The pulp builds up damage over its lifetime from trauma such as decay, heavy bite, face injuries, or drilling by the dentist. If the pulp experiences widespread inflammation because of too much past damage, then large parts of the pulp may die, which fuels the inflammation and causes more cells to die. Inflammation causes the pulp to swell, but since the pulp is confined to a fixed space inside the tooth, the swelling ends up squeezing the nerves in the pulp and causes severe tooth pain. The pulp inflammation would continue until the pulp is removed from the pulp space. ",
