import { toothDisease } from "data/diseases";
import { Link } from "react-router-dom";
import styled from "styled-components";

const Heading = styled.h2``;
const Subheading = styled.h3`
  margin-top: 1em;
  margin-bottom: 0.25em;
  border-bottom: 1px solid #a2a9b1;
`;
const Level3Subheading = styled.h4`
  margin-top: 1em;
  margin-bottom: 0.25em;
`;
const TableCellHeading = styled.h4`
  margin-bottom: 0.25em;
  border-bottom: 1px solid #a2a9b1;
`;
const TdAlignTop = styled.td`
  vertical-align: top;
`;
const TrHeader = styled.td`
  background-color: #facfce;
  font-weight: bold;
  text-align: center;
`;
const CenterContent = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
`;
const BlueHyperlink = styled(Link)`
  text-decoration: none;
  color: #3366cc;
  background: none;

  &:hover {
    text-decoration: underline;
  }
`;

function BlueLink({ to, children }) {
  return <BlueHyperlink to={to} onClick={() => window.scrollTo(0, 0)}>{children}</BlueHyperlink>;
}

function InfoTemplate(props) {
  const { title, printRef, children } = props;

  return (
    <div ref={printRef} {...props}>
      <Heading>{title}</Heading>
      {children}
    </div>
  );
}

export function ToothDisease(props) {
  const {
    treatmentImageWidth = "300px",
    conditionImageWidth = "200px",
    rctImageHeight = "600px",
    restoImageWidth = "400px",
    restoImageDescriptionWidth = "calc(50% - 10px)",
  } = props;

  const treatment = toothDisease;

  const pulpDescription = treatment.pulpalConsiderations.laymanDescription.split("/li");
  const restoDescription = treatment.restorativeConditions.laymanDescription.split("/li");

  return (
    <InfoTemplate title={treatment.name} {...props}>
      <Subheading>Description</Subheading>
      <p>{treatment.laymanDescription}</p>

      <CenterContent>
        <div style={{ height: treatmentImageWidth }}>
          <img src={treatment.image} alt={treatment.imageAlt} style={{ height: "100%" }} />
        </div>
      </CenterContent>

      <Subheading>{treatment.pulpalConsiderations.name}</Subheading>
      <p>{pulpDescription[0]}</p>
      <ol style={{ paddingLeft: "1.3em" }}>
        {pulpDescription.slice(1).map((item) => (
          <li>{item}</li>
        ))}
      </ol>

      <p>
        In-depth information of <strong>Root Canal Therapy</strong> can be found{" "}
        <BlueLink to={treatment.pulpalConsiderations.link}>here</BlueLink>.{" "}
      </p>

      <table>
        <thead>
          <tr>
            <th>Condition</th>
            <th>Signs and Symptoms</th>
            <th>Treatment</th>
            <th>Images</th>
          </tr>
        </thead>

        <tbody>
          {treatment.pulpalConsiderations.conditions.map((condition) => (
            <tr>
              <TrHeader>{condition.name}</TrHeader>
              <td>{condition.signs}</td>
              <td>{condition.treatment}</td>
              <td>
                <div style={{ width: conditionImageWidth }}>
                  <img src={condition.image} alt={condition.imageAlt} style={{ width: "100%" }} />
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Level3Subheading>Root Canal Treatment</Level3Subheading>
      <CenterContent>
        <div style={{ height: rctImageHeight }}>
          <img
            src={treatment.pulpalConsiderations.image}
            alt={treatment.pulpalConsiderations.imageAlt}
            style={{ height: "100%" }}
          />
        </div>
      </CenterContent>

      <Subheading>{treatment.restorativeConditions.name}</Subheading>
      {restoDescription[0].split("\n").map((line) => (
        <p>{line}</p>
      ))}
      <ul style={{ paddingLeft: "1.3em" }}>
        {restoDescription.slice(1).map((item) => {
          const [factor, explanation] = item.split(": ");
          return (
            <li>
              <strong>{factor}</strong>: {explanation}
            </li>
          );
        })}
      </ul>

      <p>
        In-depth information of <strong>Restorations (fillings)</strong> can be found{" "}
        <BlueLink to={treatment.restorativeConditions.link}>here</BlueLink>.{" "}
      </p>
      <CenterContent>
        <div style={{ width: restoImageWidth }}>
          <img
            src={treatment.restorativeConditions.image}
            alt={treatment.restorativeConditions.imageAlt}
            style={{ width: "100%" }}
          />
        </div>
      </CenterContent>

      <table>
        <colgroup>
          <col span="1" style={{ width: "15%" }} />
          <col span="1" style={{ width: "85%" }} />
        </colgroup>
        <thead>
          <tr>
            <th>Condition</th>
            <th>Explanation</th>
          </tr>
        </thead>
        <tbody>
          {treatment.restorativeConditions.conditions.map((condition) => (
            <tr>
              <TrHeader>{condition.name}</TrHeader>
              <TdAlignTop>
                <TableCellHeading>Description</TableCellHeading>
                <p>{condition.description}</p>
                <TableCellHeading>Treatment</TableCellHeading>
                <p>
                  <strong>{condition.treatment}</strong>
                  {": "}
                  {condition.rationale}
                </p>
                <div
                  style={{
                    display: "flex",
                    flexWrap: "wrap",
                    columnGap: "20px",
                  }}
                >
                  {condition.images.map((image) => (
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        width: `max(${restoImageDescriptionWidth}, ${
                          image.hasOwnProperty("imageWidth")
                            ? image.imageWidth
                            : conditionImageWidth
                        })`,
                      }}
                    >
                      <div
                        style={{
                          width: image.hasOwnProperty("imageWidth")
                            ? image.imageWidth
                            : conditionImageWidth,
                        }}
                      >
                        <img src={image.image} alt={image.imageAlt} style={{ width: "100%" }} />
                      </div>
                      <p style={{ textAlign: "center" }}>
                        {image.hasOwnProperty("displayDescription") &&
                          image.displayDescription &&
                          image.description}
                      </p>
                    </div>
                  ))}
                </div>
              </TdAlignTop>
            </tr>
          ))}
        </tbody>
      </table>
    </InfoTemplate>
  );
}

export function GumDisease(props) {
  return <InfoTemplate title="Gum Diseases" {...props}></InfoTemplate>;
}
