import styled from "styled-components";

const Heading = styled.h2``;
const Subheading = styled.h3`
  margin-top: 1em;
  margin-bottom: 0.25em;
  border-bottom: 1px solid #a2a9b1;
`;
const ListHeading = styled.h4`
  margin-top: 0.3em;
  padding-top: 0.5em;
`;
const List = styled.ul`
  padding-left: 1.3em;
  margin: 0.4em 0;
`;
const ListItem = styled.li`
  padding-left: 0.5em;
`;
const PrescriptionItem = styled.li`
  list-style-type: circle;
  margin-left: 2em;
  padding-left: 0.5em;
`;

export default function EmergencyInfo({
  name,
  description,
  prevention = "",
  management,
  source,
}) {
  return (
    <div>
      <Heading>{name}</Heading>
      <Subheading>Description</Subheading>
      {description.split("\n").map((text) => (
        <p>{text}</p>
      ))}
      {prevention && (
        <>
          <Subheading>Prevention</Subheading>
          <p>{prevention}</p>
        </>
      )}
      <Subheading>Management</Subheading>
      {management.map((type) => {
        const { indication, actions } = type;
        return (
          <>
            <ListHeading>{indication}</ListHeading>
            <List>
              {actions.map((action) => {
                if (typeof action === "string") {
                  return <ListItem>{action}</ListItem>;
                } else {
                  // typeof action === "list"
                  return action.map((medication) => (
                    <PrescriptionItem>
                      {medication.split("\n").map((text) => (
                        <div>{text}</div>
                      ))}
                    </PrescriptionItem>
                  ));
                }
              })}
            </List>
          </>
        );
      })}
      <Subheading>Source</Subheading>
      <p>{source}</p>
    </div>
  );
}
