import PageTemplate from "../PageTemplate";
import { useRef, useState } from "react";
import ClassificationSelect from "./ClassificationSelect";
import { classificationCategories } from "./classificationsCategories.js";
import { useReactToPrint } from "react-to-print";
import { PrintButton } from "components/Button";
import { useParams } from "react-router-dom";

export default function ClassificationPage() {
  const {
    defaultClassification = classificationCategories[0].classes[0].name,
  } = useParams();
  let defaultClassificationObject = classificationCategories[0].classes[0];
  const defaultCategoryObject =
    classificationCategories.find((category) =>
      category.classes.find((classification) => {
        if (
          classification.name.toLowerCase() ===
          defaultClassification.toLowerCase()
        ) {
          defaultClassificationObject = classification;
          return category;
        } else return false;
      })
    ) || classificationCategories[0];
  
  const [category, setCategory] = useState(defaultCategoryObject.category);
  const [classification, setClassification] = useState(
    defaultClassificationObject
  );

  const printRef = useRef();
  const [component, setComponent] = useState(
    defaultClassificationObject.component({ printRef })
  );
  const handlePrint = useReactToPrint({
    content: () => printRef.current,
    documentTitle: classification.hasOwnProperty("printName")
      ? classification.printName
      : classification.name,
  });

  const renderComponent = (selectedClassification) => {
    setComponent(selectedClassification.component({ printRef }));
  };

  return (
    <>
      <ClassificationSelect
        {...{
          category,
          setCategory,
          classification,
          setClassification,
          renderComponent,
        }}
      />
      <PrintButton handleClick={handlePrint} />
      {component}
    </>
  );
}
