import { closedExtraction, restorative, rootCanalTherapy } from "data/treatment-options";
import { StringHelper } from "pages/helper";
import styled from "styled-components";

const Heading = styled.h2``;
const Subheading = styled.h3`
  margin-top: 1em;
  margin-bottom: 0.25em;
  border-bottom: 1px solid #a2a9b1;
`;
const TableCellHeading = styled.h4`
  margin-bottom: 0.25em;
  border-bottom: 1px solid #a2a9b1;
`;
const TdAlignTop = styled.td`
  vertical-align: top;
`;
const TrHeader = styled.td`
  background-color: #facfce;
  font-weight: bold;
  text-align: center;
`;

function InfoTemplate(props) {
  const { title, children } = props;

  return (
    <div {...props}>
      <Heading>{title}</Heading>
      {children}
    </div>
  );
}

export function RCTInfo(props) {
  const { treatmentImageHeight = "600px", complicationImageHeight = "200px" } = props;

  const treatment = rootCanalTherapy;

  return (
    <InfoTemplate title={treatment.name} {...props}>
      <Subheading>Description</Subheading>
      <p>{treatment.laymanDescription}</p>
      <div style={{ height: treatmentImageHeight }}>
        <img
          src={treatment.image}
          alt={treatment.name + " procedure illustration"}
          style={{ height: "100%" }}
        />
      </div>

      <Subheading>Indication</Subheading>
      <p>{treatment.indication}</p>

      <Subheading>Prognosis</Subheading>
      <p>{treatment.prognosis}</p>

      <Subheading>Complications</Subheading>
      <p>{treatment.complicationResolution}</p>
      <table>
        <tbody>
          {treatment.complications.map((complication) => (
            <tr>
              <td style={{ backgroundColor: "lightgreen" }}>{complication.name}</td>
              <td>
                <p>{complication.description}</p>
                {complication.hasOwnProperty("image") && (
                  <div style={{ height: complicationImageHeight }}>
                    <img
                      src={complication.image}
                      alt={complication.name + " illustration"}
                      style={{ height: "100%" }}
                    />
                  </div>
                )}
                {complication.hasOwnProperty("images") && (
                  <div style={{ height: complicationImageHeight, display: "flex" }}>
                    {complication.images.map((image, index) => (
                      <img
                        src={image}
                        alt={complication.name + " illustration " + index}
                        style={{ height: "100%" }}
                      />
                    ))}
                  </div>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </InfoTemplate>
  );
}

export function RestorationInfo(props) {
  const { treatmentImageWidth = "50%", exampleImageWidth = "200px" } = props;

  const treatment = restorative;
  const considerations = treatment.considerations.split("/li");

  return (
    <InfoTemplate title={treatment.name} {...props}>
      <Subheading>Description</Subheading>
      <p>{treatment.laymanDescription}</p>

      <div style={{ display: "flex" }}>
        {treatment.images.map((image) => (
          <div style={{ width: treatmentImageWidth }}>
            <img
              src={image.image}
              alt={treatment.name + " procedure illustration"}
              style={{ width: "100%" }}
            />
          </div>
        ))}
      </div>

      <Subheading>Considerations</Subheading>
      <p>{considerations[0]}</p>
      <ul style={{ paddingLeft: "1.3em" }}>
        {considerations.slice(1).map((consideration) => {
          const [factor, explanation] = consideration.split(": ");
          return (
            <li>
              <strong>{factor}</strong>: {explanation}
            </li>
          );
        })}
      </ul>

      <Subheading>Categories</Subheading>
      <table>
        <thead>
          <tr>
            <th></th>
            {treatment.categories.map((category) => (
              <th>{category.name}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          <tr>
            <TrHeader>Description</TrHeader>
            {treatment.categories.map((category) => (
              <TdAlignTop>
                <p>{category.laymanDescription}</p>
              </TdAlignTop>
            ))}
          </tr>

          {treatment.attributes.map((attribute) => (
            <tr>
              <TrHeader>{StringHelper.capitalize(attribute)}</TrHeader>
              {treatment.categories.map((category) => (
                <TdAlignTop>{category[attribute]}</TdAlignTop>
              ))}
            </tr>
          ))}

          <tr>
            <TrHeader>Images</TrHeader>

            {treatment.categories.map((category) => (
              <td>
                {category.images.map((image, index) => (
                  <div style={{ width: exampleImageWidth }}>
                    <img
                      src={image.image}
                      alt={category.name + " procedure illustration " + index}
                      style={{ width: "100%" }}
                    />
                  </div>
                ))}
              </td>
            ))}
          </tr>
        </tbody>
      </table>

      <Subheading>Complications</Subheading>
      <table>
        <tbody>
          {treatment.complications.map((complication) => (
            <tr>
              <TrHeader>{complication.name}</TrHeader>

              <td>
                {
                  <div
                    style={{
                      height: complication.imageHeight,
                      float: "right",
                      paddingLeft: "10px",
                    }}
                  >
                    <img
                      src={complication.image}
                      alt={complication.name + " illustration"}
                      style={{ height: "100%" }}
                    />
                  </div>
                }
                {complication.attributes.map((attribute) => (
                  <>
                    <TableCellHeading>{StringHelper.capitalize(attribute)}</TableCellHeading>
                    <p>{complication[attribute]}</p>
                  </>
                ))}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </InfoTemplate>
  );
}

export function ClosedExtractionInfo(props) {
  const { treatmentImageWidth = "50%", prognosisImageHeight = "400px" } = props;

  const treatment = closedExtraction;
  const prognosis = treatment.prognosis;
  const postInstructions = treatment.postoperativeInstructions.split("/li");

  return (
    <InfoTemplate title={treatment.name} {...props}>
      <Subheading>Description</Subheading>
      <p>{treatment.laymanDescription}</p>

      <div style={{ display: "flex" }}>
        {treatment.images.map((image) => (
          <div style={{ width: treatmentImageWidth }}>
            <img src={image.image} alt={image.alt} style={{ width: "100%" }} />
          </div>
        ))}
      </div>

      <Subheading>Healing after an Extraction</Subheading>
      {prognosis.description.split("\n").map((description) => (
        <p>{description}</p>
      ))}
      <div
        style={{
          height: prognosisImageHeight,
          paddingLeft: "10px",
        }}
      >
        <img src={prognosis.image} alt={prognosis.imageAlt} style={{ height: "100%" }} />
      </div>

      <Subheading>Complications</Subheading>
      <table>
        <thead>
          <tr>
            <th colSpan="100%">Possible complications following a Tooth Extraction</th>
          </tr>
        </thead>

        <tbody>
          {treatment.complications.map((complication) => (
            <tr>
              <TrHeader>{complication.name}</TrHeader>

              <td>
                {
                  <div
                    style={{
                      height: complication.imageHeight,
                      float: "right",
                      paddingLeft: "10px",
                    }}
                  >
                    <img
                      src={complication.image}
                      alt={complication.imageAlt}
                      style={{ height: "100%" }}
                    />
                  </div>
                }
                {complication.attributes.map((attribute) => (
                  <>
                    <TableCellHeading>{StringHelper.capitalize(attribute)}</TableCellHeading>
                    <p>{complication[attribute]}</p>
                  </>
                ))}
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Subheading>Supplemental Materials</Subheading>
      <table>
        <thead>
          <tr>
            <th colSpan="100%">Possible Supplemental Materials Used During a Tooth Extraction</th>
          </tr>
        </thead>

        <tbody>
          {treatment.supplementaryMaterials.map((material) => (
            <tr>
              <TrHeader>{material.name}</TrHeader>

              <td>
                <p>{material.description}</p>
                {
                  <div
                    style={{
                      height: material.imageHeight,
                      width: "100%",
                      maxWidth: "750px",
                    }}
                  >
                    <img src={material.image} alt={material.imageAlt} style={{ height: "100%" }} />
                  </div>
                }
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Subheading>Post-Operative Care Instructions</Subheading>
      <p>{postInstructions[0]}</p>
      <ul style={{ paddingLeft: "1.3em" }}>
        {postInstructions.slice(1).map((instruction) => {
          const [factor, explanation] = instruction.includes(": ")
            ? instruction.split(": ")
            : ["", instruction];
          return (
            <li>
              <strong>{factor}</strong>
              {factor && ": "}
              {explanation}
            </li>
          );
        })}
      </ul>
    </InfoTemplate>
  );
}
