import { useState } from "react";
import TextareaAutosize from "react-textarea-autosize";
import {
  AddButton,
  RemoveButton,
  ConfirmButton,
  CancelButton,
  EditButton,
} from "../../components/Button";
import { ArrayHelper } from "../helper";
import { saveData } from "./TreatmentNotesPage";
import { DragDropContext, Droppable, Draggable } from "@hello-pangea/dnd";

function CreateModuleTextbox({
  modules,
  editing,
  createModule,
  newModuleName,
  setNewModuleName,
  selectedDiscipline,
  linkExistingModule,
}) {
  const [newModuleContent, setNewModuleContent] = useState(null);
  const [moduleExists, setModuleExists] = useState(false);

  const reset = () => {
    setNewModuleContent("");
    setNewModuleName(null);
    setModuleExists(false);
  };

  const handleCreateNewModuleButton = () => {
    if (!newModuleName) {
      return;
    } else if (modules.some((module) => module.name === newModuleName)) {
      setModuleExists(true);
      return;
    } else if (!newModuleContent) {
      return;
    }

    createModule(newModuleName, newModuleContent);
    reset();
  };

  const handleConfirmLinkExistingModuleButton = () => {
    linkExistingModule(newModuleName);
    reset();
  };

  return (
    <>
      <h3 className={editing && newModuleName !== null ? undefined : "hidden"}>
        New module
      </h3>
      <div className="flex">
        <p
          id="new-module-name-label"
          className={editing && newModuleName !== null ? undefined : "hidden"}
        >
          Module name:{" "}
        </p>
        <input
          type="text"
          value={newModuleName}
          className={editing && newModuleName !== null ? undefined : "hidden"}
          onChange={(event) => {
            setNewModuleName(event.target.value);
            setModuleExists(false);
          }}
        />

        <ConfirmButton
          className={editing && newModuleName !== null ? undefined : "hidden"}
          handleClick={handleCreateNewModuleButton}
        />

        <CancelButton
          className={editing && newModuleName !== null ? undefined : "hidden"}
          handleClick={reset}
        />
      </div>

      <div className="flex-center">
        <p className={moduleExists ? undefined : "hidden"}>
          <span className="red-text">
            Module with the name "{newModuleName}"" already exists
          </span>
          , do you want to link it under {selectedDiscipline}?
        </p>
        <ConfirmButton
          className={moduleExists ? "module-exists-button" : "hidden"}
          handleClick={handleConfirmLinkExistingModuleButton}
        />
      </div>

      <TextareaAutosize
        id="new-module-textarea"
        value={newModuleContent}
        className={
          editing && newModuleName !== null
            ? "module-textarea"
            : "module-textarea hidden"
        }
        onChange={(event) => setNewModuleContent(event.target.value)}
      />
    </>
  );
}

function ModuleList({
  modules,
  addModule,
  setModules,
  editing,
  dragging,
  setDragging,
  disciplines,
  setDisciplines,
  selectedDiscipline,
  deleteModule,
  linkExistingModule,
}) {
  const [newModuleName, setNewModuleName] = useState(null);

  const createModule = (moduleName, moduleContent) => {
    setModules([...modules, { name: moduleName, content: moduleContent }]);

    linkExistingModule(moduleName);
  };

  function ModuleCard({ moduleName, index }) {
    return (
      <Draggable
        draggableId={moduleName}
        index={index}
        isDragDisabled={!editing}
      >
        {(provided) => (
          <li
            key={moduleName}
            onClick={() =>
              editing || dragging ? undefined : addModule(moduleName)
            }
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            {moduleName}
            <RemoveButton
              className={editing ? undefined : "hidden"}
              handleClick={() => deleteModule(moduleName, selectedDiscipline)}
            />
          </li>
        )}
      </Draggable>
    );
  }

  const onDragStart = (result) => {
    setDragging(true);
  };

  const onDragEnd = (result) => {
    const { destination, source } = result;

    setDragging(false);

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.draggableId &&
      destination.index === source.index
    ) {
      return;
    }

    const disciplineIndex = disciplines.findIndex(
      (discipline) => discipline.name === selectedDiscipline
    );
    const newModules = ArrayHelper.move(
      disciplines[disciplineIndex].modules,
      source.index,
      destination.index
    );
    const newDisciplines = ArrayHelper.replace(disciplines, disciplineIndex, {
      ...disciplines[disciplineIndex],
      modules: newModules,
    });
    setDisciplines(newDisciplines);
  };

  return (
    <>
      <DragDropContext onDragStart={onDragStart} onDragEnd={onDragEnd}>
        <Droppable droppableId="modules" direction="horizontal">
          {(provided) => (
            <ul
              ref={provided.innerRef}
              {...provided.droppableProps}
              className={
                "horizontal-select module-select " +
                (editing ? undefined : "draggable")
              }
            >
              {disciplines
                .find((discipline) => discipline.name === selectedDiscipline)
                .modules.map((module, index) => (
                  <ModuleCard moduleName={module} index={index} />
                ))}
              <div>{provided.placeholder}</div>
              <AddButton
                className={
                  editing && newModuleName === null ? undefined : "hidden"
                }
                handleClick={() => setNewModuleName("")}
              />
            </ul>
          )}
        </Droppable>
      </DragDropContext>

      <CreateModuleTextbox
        {...{
          modules,
          editing,
          newModuleName,
          setNewModuleName,
          createModule,
          selectedDiscipline,
          linkExistingModule,
        }}
      />
    </>
  );
}

function CreateDisciplineTextbox({ editing, createDiscipline }) {
  const [newDiscipline, setNewDiscipline] = useState(null);

  return (
    <>
      <input
        type="text"
        value={newDiscipline}
        className={editing && newDiscipline !== null ? undefined : "hidden"}
        onChange={(event) => setNewDiscipline(event.target.value)}
      />

      <ConfirmButton
        className={editing && newDiscipline !== null ? undefined : "hidden"}
        handleClick={() => {
          createDiscipline(newDiscipline);
          if (newDiscipline !== null && newDiscipline !== "") {
            setNewDiscipline(null);
          }
        }}
      />

      <CancelButton
        className={editing && newDiscipline !== null ? undefined : "hidden"}
        handleClick={() => setNewDiscipline(null)}
      />

      <AddButton
        className={editing && newDiscipline === null ? undefined : "hidden"}
        handleClick={() => setNewDiscipline("")}
      />
    </>
  );
}

function DisciplineList({
  modules,
  disciplines,
  setDisciplines,
  selectedDiscipline,
  setSelectedDiscipline,
  editing,
  setEditing,
  dragging,
  setDragging,
  dragHandle,
}) {
  const deleteDiscipline = (deletedDiscipline) => {
    const index = disciplines.indexOf(deletedDiscipline);
    setSelectedDiscipline(disciplines[Math.max(index - 1, 0)].name);
    setDisciplines(
      disciplines.filter(
        (discipline) => discipline.name !== deletedDiscipline.name
      )
    );
  };

  function DisciplineCard({
    discipline,
    index,
    selectedDiscipline,
    setSelectedDiscipline,
  }) {
    const { name } = discipline;
    return (
      <Draggable draggableId={name} index={index} isDragDisabled={!editing}>
        {(provided) => (
          <li
            key={name}
            onMouseEnter={() =>
              dragging || editing ? undefined : setSelectedDiscipline(name)
            }
            onClick={() => setSelectedDiscipline(name)}
            className={selectedDiscipline === name ? "active" : undefined}
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            {name}
            <RemoveButton
              className={editing ? undefined : "hidden"}
              handleClick={() => deleteDiscipline(discipline)}
            />
          </li>
        )}
      </Draggable>
    );
  }

  const createDiscipline = (discipline) => {
    if (discipline === null || discipline === "") {
      return;
    }

    setDisciplines([...disciplines, { name: discipline, modules: [] }]);
  };

  const onDragStart = (result) => {
    setDragging(true);
  };

  const onDragEnd = (result) => {
    const { destination, source } = result;

    setDragging(false);

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.draggableId &&
      destination.index === source.index
    ) {
      return;
    }

    setDisciplines(
      ArrayHelper.move(disciplines, source.index, destination.index)
    );
  };

  return (
    <DragDropContext onDragStart={onDragStart} onDragEnd={onDragEnd}>
      <Droppable droppableId="disciplines" direction="horizontal">
        {(provided) => (
          <ul
            ref={provided.innerRef}
            {...provided.droppableProps}
            className="horizontal-select"
          >
            {dragHandle}
            {disciplines.map((discipline, index) => (
              <DisciplineCard
                {...{
                  discipline,
                  index,
                  selectedDiscipline,
                  setSelectedDiscipline,
                }}
              />
            ))}

            <div>{provided.placeholder}</div>

            <CreateDisciplineTextbox {...{ editing, createDiscipline }} />

            <EditButton
              handleClick={() => {
                if (editing) {
                  saveData(modules, disciplines);
                }
                setEditing(!editing);
              }}
              editing={editing}
            />
          </ul>
        )}
      </Droppable>
    </DragDropContext>
  );
}

export default function ModuleSelect({
  modules,
  setModules,
  disciplines,
  setDisciplines,
  addModule,
  deleteModule,
  dragHandle,
}) {
  const [editing, setEditing] = useState(false);
  const [selectedDiscipline, setSelectedDiscipline] = useState(
    disciplines[0].name
  );
  const [dragging, setDragging] = useState(false);

  const linkExistingModule = (moduleName) => {
    const discipline = disciplines.find(
      (discipline) => discipline.name === selectedDiscipline
    );

    if (discipline.modules.includes(moduleName)) {
      return;
    }

    const newDisciplines = ArrayHelper.replace(
      disciplines,
      disciplines.indexOf(discipline),
      { name: discipline.name, modules: [...discipline.modules, moduleName] }
    );

    setDisciplines(newDisciplines);
  };

  return (
    <div className="white-background">
      <DisciplineList
        {...{
          modules,
          disciplines,
          setDisciplines,
          selectedDiscipline,
          setSelectedDiscipline,
          editing,
          setEditing,
          dragging,
          setDragging,
          dragHandle,
        }}
      />
      <ModuleList
        {...{
          modules,
          addModule,
          setModules,
          editing,
          dragging,
          setDragging,
          disciplines,
          setDisciplines,
          selectedDiscipline,
          deleteModule,
          linkExistingModule,
        }}
      />
    </div>
  );
}
