import styled from "styled-components";
import { useState } from "react";
import { ConfirmButton, CancelButton, RemoveButton } from "../../components/Button";
import { ArrayHelper, CacheHelper } from "../helper";
import { templateDataPath } from "./TreatmentNotesPage";

const Container = styled.div`
  border: 1px solid lightgrey;
  border-radius: 2px;
  padding: 4px 8px;
  background-color: lightblue;

  display: flex;
  flex-wrap: wrap;
  column-gap: 8px;
`;

const Card = styled.button`
  border: none;
  border-radius: 2px;
  padding: 6px 4px;
  margin: 4px 0;
  background-color: white;
  cursor: pointer;

  &:hover {
    background-color: lightgreen;
    transform: translateY(-1px);
    transition: background-color 0.05s ease;
  }

  &:active {
    transform: translateY(1px);
  }
`;

function TemplateCard(props) {
  const { name, editing, deleteTemplate } = props;
  return (
    <Card {...props}>
      {name}
      <RemoveButton className={editing ? undefined : "hidden"} handleClick={deleteTemplate} />
    </Card>
  );
}

const saveTemplates = (templates) => {
  CacheHelper.localSaveCache(templateDataPath, templates);
};

export default function TemplateSelect({ templates, setTemplates, modules, setModules }) {
  const [editing, setEditing] = useState(false);
  const [newTemplateName, setNewTemplateName] = useState("");
  const [templateExists, setTemplateExists] = useState(false);
  const [chosenTemplateName, setChosenTemplateName] = useState("");

  const reset = () => {
    setEditing(false);
    setNewTemplateName("");
    setTemplateExists(false);
    setChosenTemplateName("");
  };

  const deployTemplate = () => {
    if (!chosenTemplateName) {
      return;
    }
    if (!templates.some((template) => template.name === chosenTemplateName)) {
      return;
    }

    const template = templates.find((template) => template.name === chosenTemplateName);
    setModules(template.modules);

    reset();
  };

  const createTemplate = () => {
    if (!newTemplateName) {
      return;
    } else if (templates.some((template) => template.name === newTemplateName)) {
      setTemplateExists(true);
      return;
    }

    const newTemplates = [...templates, { name: newTemplateName, modules: modules }];

    setTemplates([...templates, { name: newTemplateName, modules: modules }]);

    saveTemplates(newTemplates);
    reset();
  };

  const replaceTemplate = () => {
    if (!newTemplateName) {
      return;
    } else if (!templates.some((template) => template.name === newTemplateName)) {
      return;
    }

    const template = templates.find((template) => template.name === newTemplateName);

    const newTemplates = ArrayHelper.replace(templates, templates.indexOf(template), {
      name: template.name,
      modules: modules,
    });

    setTemplates(newTemplates);

    saveTemplates(newTemplates);
    reset();
  };

  const deleteTemplate = (templateName) => {
    const newTemplates = templates.filter((template) => template.name !== templateName);

    setTemplates(newTemplates);
    saveTemplates(newTemplates);
  };

  return (
    <>
      <h3>Templates</h3>
      <Container>
        {templates.map((template) => (
          <TemplateCard
            name={template.name}
            editing={editing}
            deleteTemplate={() => deleteTemplate(template.name)}
            onClick={() => (editing ? undefined : setChosenTemplateName(template.name))}
          />
        ))}
        <TemplateCard
          name="+ New Template"
          className={editing ? "hidden" : undefined}
          style={{
            backgroundColor: "lightgreen",
            color: "black",
            fontWeight: "bold",
          }}
          onClick={() => {
            reset();
            setEditing(!editing);
          }}
        />

        <input
          type="text"
          value={newTemplateName}
          className={editing ? undefined : "hidden"}
          placeholder=" Name of New Template"
          onChange={(event) => {
            setNewTemplateName(event.target.value);
            setTemplateExists(false);
          }}
        />

        <ConfirmButton className={editing ? undefined : "hidden"} handleClick={createTemplate} />

        <CancelButton className={editing ? undefined : "hidden"} handleClick={reset} />
      </Container>

      <div className="flex-center">
        <p className={templateExists ? undefined : "hidden"}>
          <span className="red-text">
            Template with the name "{newTemplateName}"" already exists
          </span>
          , do you want to replace it?
        </p>
        <ConfirmButton
          className={templateExists ? "module-exists-button" : "hidden"}
          handleClick={replaceTemplate}
        />
        <CancelButton
          className={templateExists ? "module-exists-button" : "hidden"}
          handleClick={() => setTemplateExists(false)}
        />
      </div>

      <div className="flex-center">
        <p className={!chosenTemplateName ? "hidden" : undefined}>
          Do you want to <span className="red-text">override</span> this page with the template "
          <span className="red-text">{chosenTemplateName}</span>
          "?
        </p>
        <ConfirmButton
          className={!chosenTemplateName ? "hidden" : "module-exists-button"}
          handleClick={deployTemplate}
        />
        <CancelButton
          className={!chosenTemplateName ? "hidden" : "module-exists-button"}
          handleClick={() => setChosenTemplateName("")}
        />
      </div>
    </>
  );
}
