import { Link } from "react-router-dom";
import PageTemplate from "./PageTemplate";
import { apps } from "./apps";

function AppTemplate({ name, link, description }) {
  return (
    <div>
      <h2>
        <Link to={link}>{name}</Link>
      </h2>
      <p>{description}</p>
    </div>
  );
}

export default function TitlePage() {
  return (
    <>
      {apps.map((app) => (
        <AppTemplate {...app} />
      ))}
    </>
  );
}
