import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

const Title = styled.h3`
  align-self: flex-start;
`;

const Card = styled.button`
  text-align: left;
  background-color: ${(props) =>
    props.selected ? "lightgreen" : props.highlighted ? "gold" : "#666"};
  padding: 0.5em 1em;
  color: ${(props) => (props.selected || props.highlighted ? "black" : "white")};
  cursor: pointer;

  &:hover {
    background-color: ${(props) => (props.selected ? "lightgreen" : "white")};
    color: black;
  }
`;

/*
  options: list(dict(name, onClick))
*/
export default function MedicationMenu({ title, options, selected }) {
  return (
    <Container>
      <Title>{title}</Title>
      <Row>
        {options.map((option) => (
          <Card
            onClick={option.onClick}
            selected={option.name === selected}
            highlighted={option.containsSearchTerm}
          >
            {option.name}
          </Card>
        ))}
      </Row>
    </Container>
  );
}
